/* ...原有樣式... */
@media screen and (orientation: landscape) {
    .scoreboard-container {
        display: flex;
        justify-content: space-between;
        /* 將子元素水平分佈，兩邊空間相等 */
        height: 100vh;
        /* 使用整個視窗高度 */
    }

    .score-container-left {
        position: relative;
        width: 48%;
        /* 調整寬度，以便兩邊有間隙 */
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: darkcyan;
    }

    .score-container-right {
        position: relative;
        width: 48%;
        /* 調整寬度，以便兩邊有間隙 */
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: darkslategray;
    }

    .score-container-mid {
        position: relative;
        width: 4%;
        /* 調整寬度，以便兩邊有間隙 */
        height: 100%;
        color: white;
        font-size: 10px;
        background-color: black;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .score-display {
        font-size: 240px;
        color: white;
    }

    .team-container.left-top {
        position: absolute;
        left: 0;
        top: 0;
        width: 48%;
        height: 50%;
        cursor: pointer;
    }

    .team-container.left-bottom {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 48%;
        height: 50%;
        cursor: pointer;
    }

    .team-container.right-top {
        position: absolute;
        right: 0;
        top: 0;
        width: 48%;
        height: 50%;
        cursor: pointer;
    }

    .team-container.right-bottom {
        position: absolute;
        right: 0;
        bottom: 0;
        width: 48%;
        height: 50%;
        cursor: pointer;
    }
}

